:root {
  --primary-color: #4caf50;
  --secondary-color: #ff9800;
  --background-color: #f0f2f5;
  --card-background: #fff;
  --text-color: #333;
  --light-text-color: #666;
  --white-color: #fff;
  --border-color: #e0e0e0;
  --shadow-light: #0000001a;
  --shadow-dark: #0003;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Nunito, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
}

.home-page {
  flex-direction: column;
  gap: 2rem;
  padding: 2rem 1.5rem 100px;
  display: flex;
}

.section-title {
  color: var(--text-color);
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 700;
}

.streak-tracker {
  width: 100%;
}

.streak-circles {
  justify-content: center;
  gap: 1.5rem;
  display: flex;
}

.streak-circle {
  background-color: var(--card-background);
  border: 3px solid var(--border-color);
  cursor: pointer;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  transition: all .4s;
}

.streak-circle.completed {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  transform: scale(1.1);
  box-shadow: 0 4px 15px #4caf5066;
}

.daily-task-card {
  background: var(--card-background);
  text-align: center;
  box-shadow: 0 10px 30px var(--shadow-light);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
}

.task-card-content h3 {
  color: var(--light-text-color);
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: 600;
}

.task-card-content p {
  color: var(--text-color);
  justify-content: center;
  align-items: center;
  min-height: 80px;
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
}

#prove-task-btn {
  background: linear-gradient(45deg, var(--secondary-color), #ffbf00);
  width: 100%;
  color: var(--white-color);
  cursor: pointer;
  border: none;
  border-radius: 15px;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  transition: all .3s;
  box-shadow: 0 4px 20px #ff98004d;
}

#prove-task-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 7px 25px #ff980066;
}

#prove-task-btn i {
  margin-right: .5rem;
}

.bottom-nav {
  background-color: var(--card-background);
  width: 100%;
  height: 80px;
  box-shadow: 0 -5px 20px var(--shadow-light);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.nav-item {
  color: var(--light-text-color);
  flex-direction: column;
  align-items: center;
  font-size: .8rem;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav-item i {
  margin-bottom: .25rem;
  font-size: 1.5rem;
}

.nav-item.active {
  color: var(--primary-color);
  transform: translateY(-5px);
}

.animate-fade-in {
  animation: .8s ease-out forwards fadeIn;
}

.animate-slide-up {
  animation: .8s ease-out forwards slideUp;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#auth-page-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  display: flex;
}

.toggle-panel .app-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
}

.auth-container-animated {
  background-color: var(--card-background);
  box-shadow: 0 5px 15px var(--shadow-dark);
  border-radius: 30px;
  width: 100%;
  max-width: 768px;
  min-height: 480px;
  position: relative;
  overflow: hidden;
}

.auth-container-animated p {
  letter-spacing: .3px;
  margin: 15px 0;
  font-size: 14px;
  line-height: 20px;
}

.auth-container-animated span {
  color: var(--light-text-color);
  margin-bottom: 10px;
  font-size: 12px;
}

.auth-container-animated a {
  color: var(--text-color);
  margin: 10px 0;
  font-size: 13px;
  text-decoration: none;
}

.auth-container-animated form {
  background-color: var(--card-background);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 40px;
  display: flex;
}

.auth-container-animated input {
  box-sizing: border-box;
  background-color: #eee;
  border: none;
  border-radius: 8px;
  outline: none;
  width: 100%;
  height: 40px;
  margin: 8px 0;
  padding: 10px 15px;
  font-size: 13px;
}

#forgot-password-link {
  margin-top: 15px;
}

@media (width >= 769px) {
  .form-container {
    height: 100%;
    transition: all .6s ease-in-out;
    position: absolute;
    top: 0;
  }

  .sign-in {
    z-index: 2;
    width: 50%;
    left: 0;
  }

  .sign-up {
    opacity: 0;
    z-index: 1;
    width: 50%;
    left: 0;
  }

  .auth-container-animated.active .sign-in {
    opacity: 0;
    transform: translateX(100%);
  }

  .auth-container-animated.active .sign-up {
    opacity: 1;
    z-index: 5;
    animation: .6s move;
    transform: translateX(100%);
  }

  @keyframes move {
    0%, 49.99% {
      opacity: 0;
      z-index: 1;
    }

    50%, 100% {
      opacity: 1;
      z-index: 5;
    }
  }
}

.social-icons {
  margin: 20px 0;
}

.social-icons a {
  border: 1px solid var(--border-color);
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 0 3px;
  transition: all .2s ease-in-out;
  display: inline-flex;
}

.social-icons a:hover {
  border-color: var(--primary-color);
  transform: scale(1.1);
}

@media (width >= 769px) {
  .toggle-container {
    z-index: 1000;
    border-radius: 150px 0 0 150px;
    width: 50%;
    height: 100%;
    transition: all .6s ease-in-out;
    position: absolute;
    top: 0;
    left: 50%;
    overflow: hidden;
  }

  .auth-container-animated.active .toggle-container {
    border-radius: 0 150px 150px 0;
    transform: translateX(-100%);
  }

  .toggle {
    background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
    color: var(--white-color);
    width: 200%;
    height: 100%;
    transition: all .6s ease-in-out;
    position: relative;
    left: -100%;
    transform: translateX(0);
  }

  .auth-container-animated.active .toggle {
    transform: translateX(50%);
  }

  .toggle-panel {
    text-align: center;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    padding: 0 30px;
    transition: all .6s ease-in-out;
    display: flex;
    position: absolute;
    top: 0;
    transform: translateX(0);
  }

  .toggle-left {
    transform: translateX(-200%);
  }

  .auth-container-animated.active .toggle-left {
    transform: translateX(0);
  }

  .toggle-right {
    right: 0;
    transform: translateX(0);
  }

  .auth-container-animated.active .toggle-right {
    transform: translateX(200%);
  }
}

.password-strength-meter, #login-button-container {
  cursor: not-allowed;
  background-color: #e0e0e0;
  border-radius: 8px;
  width: 100%;
  height: 40px;
  margin: 15px 0;
  transition: background-color .4s ease-in-out;
  position: relative;
  overflow: hidden;
}

.password-strength-meter .strength-bar {
  background-color: var(--invalid-color);
  z-index: 1;
  border-radius: 4px;
  width: 0%;
  height: 100%;
  transition: width .4s ease-in-out, background-color .4s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

#strength-text, #login-text {
  z-index: 2;
  width: 100%;
  height: 100%;
  color: var(--text-color);
  white-space: nowrap;
  text-transform: uppercase;
  letter-spacing: .5px;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 600;
  transition: color .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.strength-bar.weak {
  background-color: #ff4d4d;
}

.strength-bar.medium {
  background-color: #ffc107;
}

.strength-bar.strong {
  background-color: var(--valid-color);
}

#login-button-container.transformed, .password-strength-meter.transformed {
  background-color: var(--primary-color);
  cursor: pointer;
}

#login-button-container.transformed #login-text, .password-strength-meter.transformed #strength-text {
  color: var(--white-color);
}

.auth-container-standalone {
  background-color: var(--card-background);
  box-shadow: 0 5px 15px var(--shadow-dark);
  border-radius: 30px;
  width: 100%;
  max-width: 480px;
  padding: 40px;
  position: relative;
  overflow: hidden;
}

.auth-container-standalone form {
  flex-direction: column;
  align-items: center;
  width: 100%;
  transition: opacity .3s, visibility .3s;
  display: flex;
}

.auth-container-standalone form.hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.auth-container-standalone h1 {
  text-align: center;
  margin-bottom: 1rem;
}

.auth-container-standalone span {
  color: var(--light-text-color);
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 14px;
  display: block;
}

.auth-container-standalone .back-to-login {
  margin-top: 1.5rem;
  font-size: 14px;
}

.auth-container-standalone button {
  background-color: var(--primary-color);
  width: 100%;
  color: var(--white-color);
  letter-spacing: .5px;
  text-transform: uppercase;
  cursor: pointer;
  box-sizing: border-box;
  border: 1px solid #0000;
  border-radius: 8px;
  height: 40px;
  margin-top: 10px;
  padding: 10px 45px;
  font-size: 13px;
  font-weight: 600;
  transition: all .2s ease-in-out;
}

.auth-container-standalone input {
  box-sizing: border-box;
  background-color: #eee;
  border: none;
  border-radius: 8px;
  outline: none;
  width: 100%;
  height: 40px;
  margin: 8px 0;
  padding: 10px 15px;
  font-size: 13px;
}

.mobile-logo-container {
  display: none;
}

@media (width <= 768px) {
  #auth-page-container {
    padding: 0;
  }

  .auth-container-animated, .auth-container-standalone {
    max-width: 100%;
    min-height: 100vh;
    box-shadow: none;
    border-radius: 0;
    display: block;
  }

  .toggle-container {
    display: none;
  }

  .form-container {
    width: 100%;
    height: auto;
    padding: 40px 20px;
    display: none;
    position: relative;
  }

  .mobile-toggle-form {
    text-align: center;
    margin-top: 20px;
    display: block;
  }

  .mobile-toggle-form a {
    color: var(--primary-color);
    cursor: pointer;
    font-weight: bold;
  }

  .mobile-logo-container {
    display: block;
  }

  .toggle-panel .app-logo {
    display: none;
  }

  .home-page {
    padding: 1.5rem 1rem 100px;
  }

  .section-title {
    font-size: 1.1rem;
  }

  .streak-circle {
    width: 50px;
    height: 50px;
  }

  .daily-task-card {
    padding: 1.5rem;
  }

  .task-card-content p {
    font-size: 1.3rem;
  }
}
